  top: 0;
  left: 0;
}

.loader {
  background-color: rgba(0, 0, 0, 0);
  position: relative;
  top: 50vh;
  left: 50vw;
  transform: translateX(-50%);
}

@media screen and (max-width: 479px) {
  .loader .loader__img {
    width: 60px;
    height: 60px;
  }
}

@media screen and (min-width: 768px) {
