  animation-duration: .5s;
  animation-iteration-count: infinite;
}

@keyframes glowing {
  0% {
    opacity: 1;
    filter: blur(1px);
    text-shadow: 0 0 10px #ff6b08, 0 0 20px #ff6b08, 0 0 40px #ff6b08, 0 0 80px #ff6b08, 0 0 160px #ff6b08, 0 0 320px #ff6b08, 0 0 640px #ff6b08;
