.header {
	position: relative;
	background-image: url("../images/header-bg.jpg");
	background-size: cover;
	background-repeat: no-repeat;
	font-family: "Roboto", sans-serif;

	@media (min-width: 1280px) {
		background-image: url("../images/header-bg.jpg");
	}

	@media (min-width: 768px) {
		background-image: url("../images/header-bg-tablet.jpg");
	}

	@media (max-width: 767px) {
		background-image: url("../images/header-bg-phone.jpg");
	}
}

.header-thumb {
	position: absolute;
	background: rgba(0, 0, 0, 0.56);
	width: 100%;
	height: 100%;
}

.hidden {
	display: none;
}

.header-container {
	padding-top: 40px;
	padding-bottom: 92px;
	@media screen and (min-width: 768px) {
		padding-bottom: 80px;
	}
}

.header-menu {
	display: flex;
	margin-bottom: 40px;
}

.header-menu__logo {
	display: flex;
	align-items: center;
	margin-right: auto;
	z-index: 1;
}

.menu-logo__logo {
	width: 24px;
	height: 25px;
	margin-right: 10px;
}

.menu-logo__text {
	display: contents;

	@media screen and (max-width: 767px) {
		display: none;
	}

	font-size: 30px;
	color: var(--secondary-white-text-color);
	margin: 0 0;
}

.header-menu__logo:hover,
.header-menu__logo:focus {
	.logo-text__animation {
		opacity: 0.1;
		animation-name: glowing;
		animation-duration: 500ms;
		animation-iteration-count: infinite;
  }}


	@keyframes glowing {
		0% {
			opacity: 1;
			filter: blur(1px);
			text-shadow: 0 0 10px rgba(255, 107, 8, 1), 0 0 20px rgba(255, 107, 8, 1), 0 0 40px rgba(255, 107, 8, 1),
				0 0 80px rgba(255, 107, 8, 1), 0 0 160px rgba(255, 107, 8, 1), 0 0 320px rgba(255, 107, 8, 1),
				0 0 640px rgba(255, 107, 8, 1);
		}

		97% {
			opacity: 1;
			filter: blur(0);
			text-shadow: none;
		}
	}

.menu-logo__text {
    display: contents;

    @media screen and (max-width: 767px) {
        display: none;
    }

    font-size: 30px;
    color: var(--secondary-white-text-color);
    margin: 0 0;
}

.header-menu__logo:hover, 
.header-menu__logo:focus{

    .menu-logo__text_animation {
    
            opacity: 0.1;
            animation-name: glowing;
            animation-duration: 1s;
            animation-iteration-count: infinite;
        }
    
        @keyframes glowing {
            0% {
                opacity: 1;
                filter: blur(1px);
                text-shadow: 0 0 10px rgba(255, 107, 8, 1),
                    0 0 20px rgba(255, 107, 8, 1),
                    0 0 40px rgba(255, 107, 8, 1),
                    0 0 80px rgba(255, 107, 8, 1),
                    0 0 160px rgba(255, 107, 8, 1),
                    0 0 320px rgba(255, 107, 8, 1),
                    0 0 640px rgba(255, 107, 8, 1);
            }
    
            97% {
                opacity: 1;
                filter: blur(0);
                text-shadow: none;
            }
    
        }

}

.header-menu__nav {
	display: flex;
}

.menu-nav__link {
	&:not(:last-child) {
		margin-right: 39px;
	}
}

.nav-link__item {
	position: relative;
	padding: 10px 0;
	font-size: 12px;
	color: var(--secondary-white-text-color);
	background-color: transparent;
	border: none;
	cursor: pointer;
}

.current {
	&::after {
		content: "";
		position: absolute;
		bottom: 5px;
		left: 0;
		height: 3px;
		width: 100%;
		background-color: var(--third-red-text-color);
		border-radius: 2px;
		transition: background-color, opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
	}
}

.header-search {
	position: relative;
}

.search-input {
	display: block;
	margin: 0 auto;
	border: none;
	border-bottom: 0.5px solid gray;
	background: none;
	outline: none;
	color: var(--secondary-white-text-color);

	&:focus {
		border-bottom: 0.5px solid var(--secondary-white-text-color);
	}

	@media screen and (max-width: 767px) {
		width: 100%;
	}
	@media screen and (min-width: 768px) and (max-width: 1279px) {
		width: 336px;
	}
	@media screen and (min-width: 1280px) {
		width: 394px;
	}
}

.search-input::placeholder {
	color: gray;

	&:focus {
		background: white;
		color: #fff;
	}
}

.search-icon {
	position: absolute;
	width: 15px;
	height: 15px;
	fill: gray;
	right: 0;
	top: 0;

	&:hover {
		cursor: pointer;
	}

	@media screen and (min-width: 768px) and (max-width: 1279px) {
		right: 185px;
	}
	@media screen and (min-width: 1280px) {
		right: 410px;
	}
}

.header-search__container {
	display: flex;
	justify-content: center;
}

.unvisible {
	display: none;
}

.library-button {
	font-size: 12px;
	padding: 13px 36px;
	background-color: transparent;
	border: 1px solid var(--secondary-white-text-color);
	color: var(--secondary-white-text-color);
	border-radius: 5px;
	cursor: pointer;
	filter: drop-shadow(0px 8px 43px rgba(255, 107, 1, 0.6));

	&:hover {
		background-color: var(--accent-color);
		border: 1px solid var(--accent-color);
	}

	&:focus {
		background-color: var(--accent-color);
		border: 1px solid var(--accent-color);
	}

	@media (min-width: 768px) {
		padding: 13px 46px;
	}

	@media (min-width: 1280px) {
		padding: 13px 41px;
	}
}

.library-first {
	margin-right: 20px;
}

.library-second {
	@media (min-width: 768px) {
		padding: 13px 57px;
	}
}
